import React from "react";
import { Header } from "./Header";
import type { IPaasHeader } from "./header.d";

import { PaasTerm } from "./styles";

export function PaasHeader({ currentTermIndex, terms }: IPaasHeader) {
  return (
    <Header currentTermIndex={currentTermIndex}>
      {terms.map((t, index) => (
        <PaasTerm key={index} href={t.href ?? undefined} onClick={t.action}>
          {t.title}
        </PaasTerm>
      ))}
    </Header>
  );
}
